* {
  box-sizing: border-box;
}

body {
  margin: 0;
}

.search-form {
  background-color: #0084ff;
  justify-content: center;
  align-items: center;
  padding: 10px 0;
  display: flex;
  box-shadow: 1px 1px 10px 1px;
}

.search-input {
  width: 400px;
  padding: 10px 5px;
}

.search-button {
  height: 37px;
  cursor: pointer;
  border: none;
}

.gallery {
  width: 100%;
  flex-wrap: wrap;
  display: flex;
}

.photo-card {
  width: calc(25% - 30px);
  flex-direction: column;
  margin: 10px;
  display: flex;
}

.photo-card img {
  height: 300px;
  width: 100%;
  display: block;
}

.info {
  justify-content: space-between;
  padding: 10px 15px;
  display: flex;
  box-shadow: 1px 5px 10px;
}

.info-item {
  height: 40px;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  margin: 0;
  display: flex;
}

.load-more {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  margin: 20px auto;
  padding: 10px;
  transition: all .25s linear;
  display: none;
  box-shadow: 0 1px 13px 4px rgba(0, 0, 0, .75);
}

